import React from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'
import './App.css'
import PanelComponent from './Components/PanelComponent.js'
import IBGEComponent from './Components/IBGEComponent.js'
// import WeightComponent from './Components/WeightComponent.js'
import MyInflationComponent from './Components/MyInflationComponent.js'
import { createTheme, ThemeProvider } from '@mui/material/styles';

const generatePanel = (type, format, title, src, obs) => {
  return (
    <ScrollableAnchor id={type}>
      <div>
        <PanelComponent
          type={type}
          format={format}
          title={title}
          src={src}
          obs={obs}
        />
      </div>
    </ScrollableAnchor>
  )
}

const theme = createTheme({
  palette: {
    custom: {
      light: '#282c34',
      main: '#282c34',
      dark: 'black',
      contrastText: '#fff',
    }
  },
});

function App() {
  return (
    <div className="App">
      <div className="header">
        <h1> CONSIDERE A INFLAO </h1>
        <p> Todos os valores abaixo esto corrigidos pelo IPCA. </p>
      </div>
      <ThemeProvider theme={theme}>
        <IBGEComponent />
      </ThemeProvider>
      {/* <ThemeProvider theme={theme}>
        <WeightComponent />
      </ThemeProvider> */}
      {generatePanel("dolar", "currency", "Cotao do Dlar", "http://ipeadata.gov.br/exibeserie.aspx?serid=38389 e https://fred.stlouisfed.org/series/CPIAUCSL", "Ajustado pela inflao brasileira e americana")}
      {generatePanel("ibov", "number", "IBOVESPA", "B3")}
      {generatePanel("minimo", "currency", "Salrio Mnimo", "http://www.ipeadata.gov.br/ExibeSerie.aspx?stub=1&serid1739471028=1739471028")}
      {generatePanel("onibus", "currency", "Passagem de nibus em So Paulo", "http://www.sptrans.com.br/sptrans/tarifas/")}
      {generatePanel("gasolina", "currency", "Preo do litro de gasolina", "https://www.gov.br/anp/pt-br/assuntos/precos-e-defesa-da-concorrencia/precos/precos-revenda-e-de-distribuicao-combustiveis/serie-historica-do-levantamento-de-precos")}
      {generatePanel("gas", "currency", "Preo do botijo de gs", "https://www.gov.br/anp/pt-br/assuntos/precos-e-defesa-da-concorrencia/precos/precos-revenda-e-de-distribuicao-combustiveis/serie-historica-do-levantamento-de-precos")}
      {generatePanel("energia", "currency", "Tarifa mdia de energia eltrica residencial (R$/MWh)", "https://portalrelatorios.aneel.gov.br/mercado/cativo#!")}
      {generatePanel("cesta", "currency", "Custo da cesta bsica", "https://www.dieese.org.br/cesta/")}      
      {generatePanel("fipezap", "number", "Imveis em So Paulo (FIPEZAP)", "https://www.fipe.org.br/pt-br/indices/fipezap/#fipezap-historico")}
      {generatePanel("aluguelsp", "number", "Aluguis em So Paulo (FIPEZAP)", "https://www.fipe.org.br/pt-br/indices/fipezap/#fipezap-historico")}
      {/* {generatePanel("passagem", "currency", "Preo mdio da passagem area domstica", "https://sistemas.anac.gov.br/sas/downloads/view/frmDownload.aspx")} */}
      {generatePanel("plano", "currency", "Preo mdio do plano de sade", "https://sidra.ibge.gov.br/pesquisa/snipc")}
      {generatePanel("stf", "currency", "Salrio dos ministros do STF", "http://qualidade.ieprev.com.br/UserFiles/File/tabela%20do%20subsidios%20dos%20ministros%202015(2).pdf")}
      {generatePanel("bigmac", "currency", "Preo do Big Mac", "https://truflation.com/marketplace/big-mac-index-brazil")}
      {generatePanel("gold", "currency", "Preo do ouro (oz)", "https://pt.bullion-rates.com/gold/BRL-history.htm")}
      {generatePanel("ipca", "percentage", "IPCA mensal", "http://www.ipeadata.gov.br/ExibeSerie.aspx?serid=36482&module=M", "IPCA no est ajustado, por ser a prpria inflao")}
      {generatePanel("ipca12", "percentage", "IPCA acumulado (12 meses)", "http://www.ipeadata.gov.br/ExibeSerie.aspx?serid=36482&module=M", "IPCA no est ajustado, por ser a prpria inflao")}
      <ThemeProvider theme={theme}>
        <MyInflationComponent />
      </ThemeProvider>
      <div className="footer">
        <p> Dvidas, sugestes e contribuies em: <a href="https://github.com/danielbm/considere">https://github.com/danielbm/considere</a> e <a href="https://twitter.com/danielbrasilm1">https://twitter.com/danielbrasilm1</a> </p>
        <p> Conhea tambm a calculadora de compra ou aluguel de imvel: <a href="https://www.calculadoraimovel.com.br">https://www.calculadoraimovel.com.br</a> </p>
      </div>
    </div>
  );
}
// {generatePanel("juros", "percentage", "Juros real (Taxa SELIC)", "http://www.ipeadata.gov.br/ExibeSerie.aspx?serid=38402", "Valores apenas descontando a inflao do ms")}
export default App;
